.guess-like{
	padding-bottom: .19rem;

	h2{
		padding: .19rem 0;
		font-size: .24rem;
		color: #6d6d6d;
		text-align: center;
		line-height: 1;
		font-weight: lighter;
	}

	.guess-content{
		height: 2.02rem;
		font-size: 0;
		
		.guess-right{
			height: 100%;
			padding-left: 0.09rem;
			padding-right: .09rem;
			
			li{
				padding: .1rem 0 0 0;
				width: 1.7rem;
				overflow: hidden;
				text-align: center;
			}

			.goods-img{
				.img-wraper{
					width: 1.536rem;
					height: 1.536rem;
					margin: 0 auto;
					// border: 1px solid red;
				}
			}

			.price{
				padding-top: .11rem;
				text-align: center;
				line-height: 1;

				.new-price{
					color: #c81522;
					font-size: .22rem;
					padding-right: .18rem;

					em{
						font-size: .17rem;
					}
				}

				.old-price{
					color: #949493;
					font-size: .18rem;

					em{
						font-size: .15rem;
					}
				}
			}
		}

		.guess-left{
			height: 100%;
			width: 2.79rem;
			position: absolute;
			left: 0;
			top: 0;

			.guess-arrow{
				position: absolute;
				right: 0;
				top: 50%;
				@include translate(0, -50%);
				width: .11rem;
				height: .19rem;
				background: url(../images/guess-arrow.jpg) no-repeat;
				background-size: 100% 100%;
				z-index: 20;
			}

			.logo{
				text-align: center;
				padding-top: .29rem;
				
				img{
					width: auto;
					height: .49rem;
				}
			}

			.name{
				font-size: .25rem;
				color: #6d6d6d;
				padding: .14rem 0;
				line-height: 1;
				text-align: center;
			}

			.preferential{
				text-align: center;

				span{
					display: inline-block;
					height: .28rem;
					padding: 0 .18rem;
					line-height: .28rem;
					background-color: #c81522;
					color: #fff;
					font-size: .18rem;
					border-radius: .15rem;
					padding-top: .02rem;
				}
			}
		}
	}
}