.index-entry{
	ul{
		@include clearfix();

		li{
			width: 25%;
			float: left;
			padding-bottom: .2rem;

			a{
				color: #c81522;
				display: block;
				padding-top: .8rem;
				width: 100%;
				text-align: center;
				font-size: .2rem;

				&.clothes{
					background: url(../images/clothes.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.package{
					background: url(../images/package1.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.motion{
					background: url(../images/motion1.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.homelife{
					background: url(../images/homelife.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.child{
					background: url(../images/child.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.watch{
					background: url(../images/watch1.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.rushbuy{
					background: url(../images/rushbuy.jpg) no-repeat center top;
					background-size: auto .7rem;
				}

				&.card{
					background: url(../images/card.jpg) no-repeat center top;
					background-size: auto .7rem;
				}
			}
		}
	}
}