.red-package{
	width: 0.72rem;
    height: 0.72rem;
    position: fixed;
    bottom: 1.98rem;
    right: 0.18rem;
    z-index: 1000;
    // background: url(../images/red_package.png) no-repeat center center;
    // background-size: 100% 100%;

    img{
    	width: 100%;
    	height: 100%;
    	display: block;
    }
}

@media screen and (min-width: 768px){
    .red-page.mobile{
		.red-package{
			width: 0.43rem;
            height: 0.43rem;
		    position: fixed;
		    bottom: 1.68rem;
		    right: 0.18rem;
		    z-index: 1000;
		    background: url(../images/red_package.png) no-repeat center center;
		    background-size: 100% 100%;

		    img{
		    	width: 100%;
		    	height: 100%;
		    	display: block;
		    }
		}
    }
}